@use "./Colors.scss";
.list {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    margin-top: 68px;
    justify-content: space-around;
    padding-top: 40px;
    height: 60vh;
    overflow-y: scroll;
    &::-webkit-scrollbar {
        display: none;
    }
    &::-webkit-scrollbar-track {
        display: none;
    }
    &-item {
        color: #FFFFFF;
        width: 192px;
        height: 226px;
        background: Colors.$main-dark;
        margin-right: 28px;
        margin-bottom: 58px;
        border-radius: 16px;
        &-img {
            border-radius: 48%;
            margin: -38px 21px 14px;
            width: 150px;
            height: 136px;
        }
        &-name {
            text-transform: capitalize;
            width: 144px;
            height: 36px;
            overflow: hidden;
            margin: 0 24px;
            font-weight: 500;
            font-size: 14px;
            line-height: 130%;
            text-align: center;
            margin-bottom: 8px;
        }
        &-cost {
            margin: 0 auto;
            font-size: 14px;
            line-height: 140%;
            width: 37px;
            height: 20px;
            text-align: center;
            margin-bottom: 4px;
        }
        &-bowls {
            margin: 0 auto;
            color: #ABBBC2;
            font-size: 14px;
            line-height: 140%;
            width: 111px;
            height: 20px;
        }
    }
}