@use './Colors';
.sidebar {
    background-color: Colors.$main-dark;
    height: 100vh;
    width: 104px;
    border-bottom-right-radius: 16px;
    border-top-right-radius: 16px;
    &__list {
        list-style-type: none;
        padding-left: 12px;
        &-item {
            & a {
                display: inline-block;
                padding: 18px;
                border-radius: 8px;
            }
            display: inline-block;
            padding: 13px 24px 11px 12px;
            border-top-left-radius: 8px;
            border-bottom-left-radius: 8px;
            & svg {
                width: 20px;
                height: 20px;
                & path {
                    fill: Colors.$main-orange;
                }
            }
            &-active {
                background-color: Colors.$main-grey;
            }
            &-active a {
                background-color: Colors.$main-orange;
                & svg path {
                    fill: #fff;
                }
            }
        }
        &-main {
            margin: 24px;
            margin-left: 12px;
            padding: 8px;
            background-color: #543C3B;
            border-radius: 16px;
            & svg {
                width: 33px;
                height: 33px;
            }
        }
    }
}