@use "./Colors.scss";
.exit {
    margin: auto;
    width: 50vw;
    height: 50vh;
    background-color: Colors.$main-dark;
    display: flex;
    justify-content: center;
    &__message {
        color: Colors.$main-orange;
        font-size: 35px;
        margin: 15px;
    }
    &__buttons {
        display: flex;
        justify-content: space-between;
        &-item {
            font-family: Roboto;
            font-weight: 700;
            font-size: 25px;
            display: inline-block;
            background: cyan;
            padding: 10px 20px;
            cursor: pointer;
            color: royalblue;
            border-radius: 5px;
            border: 3px solid Colors.$main-orange;
            transition: 3s;
            &:hover {
                background: pink;
                box-shadow: 3px 3px 3px blue inset;
                color: red;
                border: 3px double yellow;
            }
        }
    }
    &__wait {
        display: block;
        font-size: 30px;
        color: red;
        font-style: italic;
        margin-top: 20px;
        text-align: center;
    }
}