@use "./Colors.scss";
.order {
    font-family: Barlow, Roboto, sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 140%;
    color: white;
    &__all {
        &-id {
            font-size: 20px;
            padding-bottom: 24px;
        }
        &-types {
            list-style-type: none;
            display: flex;
            margin-bottom: 24px;
            &-item {
                cursor: pointer;
                display: inline-block;
                color: Colors.$main-orange;
                background-color: Colors.$main-dark;
                border: 1px solid Colors.$main-grey;
                margin-right: 8px;
                padding: 8px 13px;
                border-radius: 10px;
                &-active {
                    background-color: Colors.$main-orange;
                    color: white;
                }
            }
        }
        &-table {
            &-name {
                display: flex;
                justify-content: space-between;
                padding-bottom: 24px;
                border-bottom: 1px solid Colors.$main-grey;
                // to delete
                margin-bottom: 24px;
                &-item {
                    font-size: 16px;
                }
                &-qty {
                    display: inline-block;
                    margin-right: -34px;
                }
            }
        }
    }
    &__button {
        background-color: Colors.$main-orange;
        width: 361px;
        height: 48px;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        border-radius: 8px;
        cursor: pointer;
    }
}