@use './Colors.scss';
.center {
    font-family: Barlow, Roboto, serif;
    &__top {
        display: flex;
        justify-content: space-between;
        &-left {
            font-style: normal;
            font-weight: 600;
            line-height: 140%;
            &-top {
                color: #fff;
                font-size: 28px;
                padding-bottom: 4px;
            }
            &-bottom {
                font-size: 16px;
                color: #E0E6E9;
            }
        }
        &-right {
            &-search {
                box-sizing: border-box;
                font-size: 14px;
                padding: 14px;
                padding-left: 42px;
                background-color: #2D303E;
                background-image: url('../vectors/search.svg');
                background-size: 15px;
                background-repeat: no-repeat;
                background-position: 17px center;
                border-radius: 8px;
                border-style: solid;
                border-color: Colors.$main-dark;
                &::placeholder {
                    color: #ABBBC2;
                    margin: 0 8px;
                    width: 180px;
                    height: 20px;
                }
            }
        }
    }
    &__bottom {
        &-list {
            font-size: 14px;
            margin-top: 24px;
            list-style-type: none;
            padding-bottom: 2px;
            border-bottom: 1px solid #393C49;
            display: flex;
            &-item {
                text-decoration: none;
                color: #fff;
                padding-right: 32px;
                &-active {
                    color: Colors.$main-orange;
                    &::after {
                        z-index: 1;
                        position: relative;
                        content: '';
                        display: inline-block;
                        width: 50%;
                        height: 2px;
                        background-color: Colors.$main-orange;
                        border-radius: 3px;
                        top: 7px;
                    }
                }
            }
        }
    }
    &__footer {
        &-sort {
            display: flex;
            justify-content: space-between;
            &-left {
                font-family: Barlow;
                margin-top: 34px;
                margin-left: 10px;
                color: white;
                font-weight: 600;
                font-size: 20px;
                line-height: 140%;
            }
            &-select {
                padding: 14px;
                padding-left: 44px;
                vertical-align: center;
                border: 1px solid #393C49;
                border-radius: 8px;
                -webkit-appearance: none;
                font-weight: 500;
                font-size: 14px;
                line-height: 130%;
                list-style-type: none;
                color: #fff;
                background-color: Colors.$main-dark;
                background-image: url('../Images/Select.png');
                background-repeat: no-repeat;
                background-position: left 14px center;
                & * {
                    border-top-left-radius: 20px;
                    display: block;
                    color: red;
                    background-color: lightblue;
                    box-sizing: content-box;
                    text-align: center;
                    padding: 5px;
                     ::before {
                        position: relative;
                        z-index: 2;
                        display: block;
                        content: '';
                        background-image: url('../Images/chef-logo.png');
                        background-repeat: no-repeat;
                        background-position: left center;
                    }
                }
            }
        }
    }
}